/* login.css */
.login-container {
  max-width: 500px;
  margin: 30px auto;
  padding: 20px;
  font-family: 'Segoe UI', sans-serif;
}

.login-heading {
  text-align: center;
  font-size: 22px;
  color: #2c3e50;
  margin-bottom: 20px;
}

.login-box {
  background: linear-gradient(to right, #f2fbff, rgb(4, 160, 244)); 
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.login-inner {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-title {
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 10px;
}

.role-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.role-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  background-color: #126dd5;
  cursor: pointer;
}

.role-btn:hover {
  opacity: 0.9;
}

.active-admin {
  background-color: #2563eb;
  color: white;
}

.active-user {
  background-color: #16a34a;
  color: white;
}

.role-separator {
  color: #aaa;
  font-weight: bold;
  
}

.login-form input {
  padding: 10px;
  margin-bottom: 10px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.login-form button {
  background-color: #05c0f8;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
}

.login-form button:hover {
  background-color:  #2563eb;
}
h5{
  text-align: center;
}

p{
  text-align: center;
}

/* Responsive */
@media (max-width: 768px) {
  .login-container {
    max-width: 100%;
    margin: 20px 10px;
    padding: 15px;
  }

  .login-heading {
    font-size: 20px;
    margin-bottom: 15px;
  }

  .login-box {
    padding: 20px;
    border-radius: 8px;
  }

  .login-title {
    font-size: 18px;
  }

  .role-buttons {
    flex-direction: column;
    gap: 8px;
  }

  .role-btn {
    padding: 12px;
    font-size: 16px;
    width: 100%;
  }

  .role-separator {
    display: none;
  }

  .login-form input {
    padding: 12px;
    font-size: 16px;
    margin-bottom: 12px;
  }

  .login-form button {
    padding: 12px;
    font-size: 16px;
    width: 100%;
  }
}

@media (max-width: 480px) {
  .login-container {
    margin: 10px 5px;
    padding: 10px;
  }

  .login-heading {
    font-size: 18px;
  }

  .login-box {
    padding: 15px;
  }

  .login-title {
    font-size: 16px;
  }

  .role-btn {
    padding: 10px;
    font-size: 14px;
  }

  .login-form input {
    padding: 10px;
    font-size: 14px;
  }

  .login-form button {
    padding: 10px;
    font-size: 14px;
  }
}
