.invoice-search-container {
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.filters input, .filters button {
  padding: 8px;
  font-size: 14px;
}

.filters button {
  background-color: #1e40af;
  color: white;
  border: none;
  cursor: pointer;
}

.filters button:hover {
  background-color: #1e3a8a;
}

.invoice-table {
  width: 100%;
  border-collapse: collapse;
}

.invoice-table th, .invoice-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.invoice-table th {
  background-color: #f1f5f9;
}

/* ðŸ“± Mobile Responsive Design */
@media (max-width: 768px) {
  .invoice-search-container {
    padding: 15px;
  }

  h2 {
    font-size: 20px;
    margin-bottom: 15px;
    text-align: center;
  }

  .filters {
    flex-direction: column;
    gap: 10px;
    margin-bottom: 15px;
  }

  .filters input,
  .filters button {
    padding: 12px;
    font-size: 16px;
    width: 100%;
  }

  .invoice-table {
    font-size: 14px;
  }

  .invoice-table th,
  .invoice-table td {
    padding: 8px 6px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .invoice-search-container {
    padding: 10px;
  }

  h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .filters {
    gap: 8px;
    margin-bottom: 10px;
  }

  .filters input,
  .filters button {
    padding: 10px;
    font-size: 14px;
  }

  .invoice-table {
    font-size: 12px;
  }

  .invoice-table th,
  .invoice-table td {
    padding: 6px 4px;
    font-size: 11px;
  }
}
