.edit-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 25px 30px;
  background: #f9f9f9;
  border-radius: 10px;
  font-family: Arial, sans-serif;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.edit-container h2 {
  text-align: center;
  margin-bottom: 25px;
  font-size: 22px;
  color: #333;
}

.edit-form label {
  display: block;
  margin-bottom: 15px;
  font-weight: 500;
  color: #444;
}

.edit-form input {
  width: 100%;
  padding: 10px 12px;
  margin-top: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 15px;
  box-sizing: border-box;
  background-color: #fff;
}

/* Plus/Minus Button Group */
.input-group {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.input-group input {
  flex: 1;
  text-align: center;
  padding: 10px 12px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background: #fff;
}

.input-group button {
  padding: 8px 14px;
  font-size: 18px;
  background-color: #e0e0e0;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.input-group button:hover {
  background-color: #d5d5d5;
}

.update-btn {
  width: 100%;
  padding: 12px;
  background-color: #0c46c4;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.update-btn:hover {
  background-color: #13379b;
}

.back-button {
  margin-top: 12px;
  width: 100%;
  padding: 10px;
  background-color: #e0e0e0;
  color: #333;
  border: none;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #d5d5d5;
}

.loading,
.error {
  text-align: center;
  font-size: 16px;
  color: #d9534f;
  margin-top: 20px;
}

/* ðŸ“± Mobile Responsive Design */
@media (max-width: 768px) {
  .edit-container {
    max-width: 100%;
    margin: 20px 10px;
    padding: 20px;
    border-radius: 8px;
  }

  .edit-container h2 {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .edit-form label {
    margin-bottom: 10px;
    font-size: 14px;
  }

  .edit-form input {
    padding: 12px;
    font-size: 16px;
  }

  .input-group {
    gap: 8px;
  }

  .input-group input {
    padding: 12px;
    font-size: 16px;
  }

  .input-group button {
    padding: 10px 12px;
    font-size: 16px;
  }

  .update-btn,
  .back-button {
    padding: 12px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .edit-container {
    margin: 10px 5px;
    padding: 15px;
  }

  .edit-container h2 {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .edit-form label {
    font-size: 13px;
  }

  .edit-form input {
    padding: 10px;
    font-size: 14px;
  }

  .input-group input {
    padding: 10px;
    font-size: 14px;
  }

  .input-group button {
    padding: 8px 10px;
    font-size: 14px;
  }

  .update-btn,
  .back-button {
    padding: 10px;
    font-size: 14px;
  }

  .loading,
  .error {
    font-size: 14px;
  }
}
