.invoice-container {
  max-width: 800px;
  margin: auto;
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 0 12px rgba(0,0,0,0.1);
  font-family: Arial, sans-serif;
}

.invoice-container h2 {
  margin-bottom: 10px;
  color: #1e293b;
}

.invoice-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.invoice-table th,
.invoice-table td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  text-align: left;
}

.totals {
  margin-top: 20px;
  font-size: 16px;
  color: #111;
}

.loading-text {
  text-align: center;
  margin-top: 40px;
  font-size: 18px;
}

/* ðŸ“± Mobile Responsive Design */
@media (max-width: 768px) {
  .invoice-container {
    max-width: 100%;
    margin: 10px;
    padding: 15px;
    border-radius: 8px;
  }

  .invoice-container h2 {
    font-size: 20px;
    margin-bottom: 15px;
    text-align: center;
  }

  .invoice-table {
    font-size: 14px;
    margin-top: 15px;
  }

  .invoice-table th,
  .invoice-table td {
    padding: 8px 6px;
    font-size: 12px;
  }

  .totals {
    margin-top: 15px;
    font-size: 14px;
    text-align: center;
  }

  .loading-text {
    margin-top: 30px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .invoice-container {
    margin: 5px;
    padding: 10px;
  }

  .invoice-container h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .invoice-table {
    font-size: 12px;
    margin-top: 10px;
  }

  .invoice-table th,
  .invoice-table td {
    padding: 6px 4px;
    font-size: 11px;
  }

  .totals {
    margin-top: 10px;
    font-size: 13px;
  }

  .loading-text {
    margin-top: 20px;
    font-size: 14px;
  }
}
